body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
header {
  background-color: #2c3e50;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  color: white;
  padding: 0 15px;
}

header h1 {
  text-align: center;
}

header li {
  list-style: none;
}

header a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
  opacity: 0.9;
}

header a:hover {
  opacity: 1;
}

header nav {
  display: -webkit-flex;
  display: flex;
}

header nav li {
  margin: 0 15px;
}

header nav li:first-child {
  margin-left: 0;
}

header nav li:last-child {
  margin-right: 0;
}

@media (max-width: 1000px) {
  header{
    padding: 20px 50px;
  }
}


@media (max-width: 700px) {
  header{
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  header h2{
    margin-bottom: 15px;
  }
}
.box {
  margin: 10px;
  height: 150px;
  min-width: 100px;
  width: 10%;
  background-color: grey;
  border: 6px solid grey;
  border-radius: 25px;
  display: inline-block;
  transition: all 0.2s ease-in;
  cursor: pointer;
}

